@import '../../../../utils/breakpoints';

.notifications {
  padding: 16px 65px;
  &__title {
    font-size: 28px;
    line-height: 125.3%;
    color: #192434;
    margin-bottom: 24px;
  }
  &__blocks {
    background: linear-gradient(0deg, rgba(25, 59, 103, 0.05), rgba(25, 59, 103, 0.05)), #FFFFFF;
    border-radius: 0px 0px 4px 4px;
  }
}

@media (max-width: $mobile-width) { 
  .notifications {
    animation: openPageTranslate ease-in-out 0.6s forwards;
    padding: 24px 20px;
    &__title {
      display: none;
    }
  }
}