@import '../../utils/breakpoints';


@keyframes toRight {
  from {
    transform: translate(1px, 1px);
  }
  to {
    transform: translate(13px, 1px);
  }
}
@keyframes toLeft {
  from {
    transform: translate(13px, 1px);
  }
  to {
    transform: translate(1px, 1px);
  }
}

.notification-block {
  padding: 16px 28px;
  display: flex;
  align-items: center;
  border: 1px solid #1A38601A;
  &__icon {
    width: 28px;
    height: 28px;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  &__title {
    font-size: 16px;
    line-height: 125.3%;
    color: #192434;
    font-weight: normal;
    margin-bottom: 4px;
  }
  &__subtitle {
    font-size: 14px;
    line-height: 134%;
    color: rgba(27, 43, 65, 0.72);
  }
  &__content {
    margin: 0px 16px;
  }
}

.switch {
  margin-left: auto;
  position: relative;
  min-width: 33px;
  min-height: 20px;
  input {
    z-index: 100;
    appearance: none;
    opacity: 1;
    width: 100%;
    height: 20px;
    display: block;
    background-color: #ccc;
    border-radius: 20px;
    &:checked {
      background-color: #00AF54;
    }
  }
  &__circle {
    position: absolute;
    width: 18px;
    height: 18px;
    background: white;
    border-radius: 50%;
    top: 0;
    transform: translate(1px, 1px);
    pointer-events: none; // for not clickable span
    &-toRight {
      animation: toRight 0.5s forwards;
    }
    &-toLeft {
      animation: toLeft 0.5s forwards;
    }
  }
}

@media (max-width: $mobile-width) { 
  .notification-block {
    padding: 16px 14px;
  }
}